import React, { useEffect, useState } from 'react';
import MainPageLayout from '../components/MainPageLayout';
import ShowGrid from '../components/show/ShowGrid';
import { useSearchShows } from '../misc/custom-hooks';
import { fetchApi } from '../misc/config';

const Header = ({ message, error }) => {
  return (
    <div className="mt-12 flex justify-center">
      <h1
        className={error ? 'font-normal text-base' : 'text-2xl font-semibold'}
      >
        {message}
      </h1>
    </div>
  );
};
const Starred = () => {
  const [starred] = useSearchShows();
  console.log(starred);
  const [show, setShow] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (starred && starred.length > 0) {
      const promises = starred.map(showId => fetchApi(`/shows/${showId}`));
      Promise.all(promises)
        .then(result => result.map(show => ({ show })))
        .then(result => {
          setShow(result);
          setIsLoading(false);
        })
        .catch(err => {
          setError(err.message);
          setIsLoading(false);
        });
    } else {
      setIsLoading(false);
    }
  }, [starred]);

  const renderAll = () => {
    if (isLoading) {
      return <Header message="Fetching data..." />;
    } else if (error) {
      return <Header error={error} message={`Error, ${error}`} />;
    } else if (!show) {
      return <Header message="No starred show!" />;
    } else if (show && show.length > 0) {
      return <ShowGrid data={show} />;
    }
    return null;
  };

  return (
    <MainPageLayout>
      <div className="">{renderAll()}</div>
    </MainPageLayout>
  );
};

export default Starred;
