import React from 'react';
import PreviewImage from '../PreviewImage';
import { Star } from './styled';

const Hero = ({ name, img, rating, summary, tags }) => {
  return (
    <div className="mt-12 w-full flex flex-wrap">
      <div className="w-full sm:w-1/2 md:w-2/5 lg:w-72">
        <PreviewImage img={img} height=" h-96 " width=" w-full " />
      </div>
      <div className="mt-8 px-0 sm:px-8 sm:mt-0 w-full sm:w-1/2 md:w-3/5 lg:w-[calc(100%-18rem)]">
        <div className="">
          <h1 className="font-bold text-3xl">{name}</h1>
          <div className="inline-flex">
            <Star className="mr-1 bg-yellow-500" />
            <span className="text-sm">{rating.average || 'N/A'}</span>
          </div>
        </div>
        <div
          className="mt-4 text-justify"
          dangerouslySetInnerHTML={{ __html: summary }}
        />

        <div className="mt-4 w-full flex flex-row flex-wrap">
          Tags:{' '}
          <div className="mt-1 w-full  flex flex-row flex-wrap gap-2">
            {tags.map((tag, i) => (
              <span
                className="rounded-full py-1 px-4 hover:bg-indigo-300 transform transition hover:translate-y-1 bg-indigo-500 text-sm text-white"
                key={i}
              >
                {tag}
              </span>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Hero;
