import React from 'react';
import { Link } from 'react-router-dom';
import PreviewImage from '../PreviewImage';
import { Star } from './../show/styled';

const ShowCard = ({ id, name, img, summary, onStarClick, isStarred }) => {
  const summaryText = summary
    ? `${summary.split(' ').slice(0, 10).join(' ').replace(/<.+?>/g, '')}...`
    : 'No description';

  return (
    <div className="w-full h-full transition-all flex flex-col items-center">
      <div className="w-full ">
        <PreviewImage img={img} height=" h-72 xl:h-80 " width=" w-full " />
      </div>
      <div className="h-full w-full flex flex-col py-2 justify-between ">
        <div className="">
          <h1 className="font-semibold">{name}</h1>
          <p className="text-sm">{summaryText}</p>
        </div>
        <div className="mt-2 w-full flex justify-between">
          <Link
            to={`/show/${id}`}
            className="text-blue-600 hover:text-blue-900 "
          >
            Read more
          </Link>
          <button
            onClick={onStarClick}
            type="button"
            className={`group px-4 pt-1 border-2 bg-white  rounded-full text-sm hover:bg-blue-300 hover:border-blue-300 hover:text-white transition duration-200 transform hover:-translate-y-1 z-10 ${
              isStarred ? 'border-blue-600' : 'border-gray-300'
            }`}
          >
            <Star
              className={
                isStarred ? 'bg-blue-500 ' : 'bg-gray-300 group-hover:bg-white'
              }
            />
          </button>
        </div>
      </div>
    </div>
  );
};

export default ShowCard;
