import React from 'react';

const Seasons = ({ seasons }) => {
  return (
    <div className="mt-8 w-full ">
      <div className="">
        <h2 className="font-semibold text-2xl">Seasons</h2>
      </div>
      <div className="mt-4">
        <p>
          Seasons in total :{' '}
          <span className="font-semibold">{seasons.length}</span>
        </p>
        <p>
          Episodes in total :{' '}
          <span className="font-semibold">
            {seasons.reduce((acc, season) => acc + season.episodeOrder, 0)}
          </span>
        </p>
        <div className="">
          {seasons.map(season => (
            <div
              className=" mt-2 flex flex-col md:flex-row md:items-center jusify-center"
              key={season.id}
            >
              <div className="w-full sm:w-1/2 md:w-96 md:border-r-2">
                <p className="font-semibold">Season {season.number}</p>
                <p>
                  Episodes :{' '}
                  <span className="font-semibold">{season.episodeOrder}</span>
                </p>
              </div>
              <div className="w-full sm:w-1/2 inline-flex ml-0 md:ml-4">
                <p>
                  Aired :{' '}
                  <span className="font-semibold">
                    {season.premiereDate} - {season.endDate}
                  </span>
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Seasons;
