import React from 'react';

const Title = ({ title, subtitle }) => (
  <div className="flex flex-col items-center">
    <h1 className="font-bold text-blue-600 text-3xl tracking-widest uppercase">
      {title}
    </h1>
    <p className="text-gray-600  font-semibold text-lg">{subtitle}</p>
  </div>
);
export default Title;
