import React from 'react';
import IMG_PLACEHOLDER from './../../img/not-found.png';

const Cast = ({ cast }) => {
  return (
    <div className="mt-8 w-full ">
      <div className="">
        <h2 className="font-semibold text-2xl">Cast</h2>
      </div>
      <div className="mt-4 w-full flex space-y-4 justify-between flex-wrap">
        {cast.map(({ person, character, voice }, key) => (
          <div
            className="gap-4 w-full md:w-1/2 lg:w-96 flex  items-center "
            key={key}
          >
            <img
              className="w-16 h-16 rounded-full object-cover"
              src={person.image ? person.image.medium : IMG_PLACEHOLDER}
              alt="cast-person"
            />
            <span>
              <span className="font-semibold">{person.name}</span> |{' '}
              {character.name} {voice ? '| Voice' : ''}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Cast;
