import React from 'react';
import { Link, useLocation } from 'react-router-dom';

const LINKS = [
  { target: '/', text: 'Home' },
  { target: '/starred', text: 'Starred' },
];

const Navs = () => {
  const location = useLocation();
  return (
    <div className="flex flex-row  mt-4 text-lg font-semibold text-gray-400">
      {LINKS.map(item => (
        <Link
          className={
            'px-2 ' +
            (location.pathname === item.target &&
              'text-blue-600 relative after:absolute after:animate-grow after:h-[2px] after:block after:bg-blue-600 after:left-0 after:right-0 after:bottom-0')
          }
          key={item.target}
          to={item.target}
        >
          {item.text}
        </Link>
      ))}
    </div>
  );
};

export default Navs;
