import React from 'react';
import PreviewImage from '../PreviewImage';

const ActorCard = ({ img, name, gender, country, birthday, deathday }) => {
  return (
    <div className="w-full transition-all flex flex-col items-center">
      <div className="w-full ">
        <PreviewImage img={img} height=" h-72 xl:h-80 " width=" w-full " />
      </div>
      <div className="w-full py-2">
        <h1>
          <span className="font-semibold">{name}</span>{' '}
          {gender ? `(${gender})` : null}
        </h1>
        <p>{country ? `${country}` : 'Unknown country'}</p>
        {birthday ? `Born ${birthday}` : null}
        <p className="font-bold mt-auto mb-0">
          {deathday ? `Died ${deathday}` : 'Alive'}
        </p>
      </div>
    </div>
  );
};

export default ActorCard;
