import { useEffect, useReducer, useState } from 'react';
import { fetchApi } from './config';

function showReducer(prevState, action) {
  switch (action.type) {
    case 'ADD': {
      return [...prevState, action.showId];
    }
    case 'REMOVE': {
      return prevState.filter(showId => showId !== action.showId);
    }
  }
}

function usePersistedReducer(reducer, initialState, key) {
  // The initial parameter returned from second argument
  const [state, dispatch] = useReducer(reducer, initialState, initial => {
    const persisted = localStorage.getItem(key);
    return persisted ? JSON.parse(persisted) : initial;
  });

  useEffect(() => {
    localStorage.setItem(key, JSON.stringify(state), [state]);
  });
  return [state, dispatch];
}

export function useSearchShows(key = 'shows') {
  return usePersistedReducer(showReducer, [], key);
}

// CustomHooks for lates search
export function useLastQuery(key = 'lastQuery') {
  const [input, setInput] = useState(() => {
    // Check if ther are p[ersisted input , if nop return nothing
    const persisted = sessionStorage.getItem(key);
    return persisted ? JSON.parse(persisted) : '';
  });

  const setPersistedInput = newQuery => {
    setInput(newQuery);
    sessionStorage.setItem(key, JSON.stringify(newQuery));
  };

  return [input, setPersistedInput];
}

// Custom hook used in ShowPage
export function useShow(showId) {
  const reducer = (prevState, action) => {
    switch (action.type) {
      case 'FETCH_SUCCESS': {
        return { isLoading: false, error: null, show: action.show };
      }
      case 'FETCH_FAILED': {
        return { isLoading: false, error: action.error, show: null };
      }
      default:
        return prevState;
    }
  };

  const [state, dispatch] = useReducer(reducer, {
    show: null,
    isLoading: true,
    error: null,
  });
  useEffect(() => {
    let isMounted = true;
    fetchApi(`/shows/${showId}?embed[]=seasons&embed[]=cast`)
      .then(result => {
        if (isMounted) {
          dispatch({ type: 'FETCH_SUCCESS', show: result });
        }
      })
      .catch(err => {
        if (isMounted) {
          dispatch({ type: 'FETCH_FAILED', error: err.message });
        }
      });
    return () => {
      isMounted = false;
    };
  }, [showId]);

  return state;
}
