import React from 'react';

const PreviewImage = ({ img, height, width }) => {
  if (img) {
    return (
      <img
        src={img.medium}
        alt=""
        className={`${height} ${width} object-cover object-center rounded-3xl`}
      />
    );
  } else {
    return (
      <div
        className={`${height} ${width} rounded-3xl bg-gray-200 flex items-center justify-center`}
      >
        <h1 className="text-2xl font-semibold whitespace-pre-wrap tracking-tight text-gray-500">
          Preview Unavaible
        </h1>
      </div>
    );
  }
};

export default PreviewImage;
