import React from 'react';
import ShowCard from './ShowCard';
import { useSearchShows } from '../../misc/custom-hooks';
import FadeIn from 'react-fade-in';

const ShowGrid = ({ data }) => {
  const [starredShow, dispatchStarred] = useSearchShows();
  return (
    <FadeIn className="mt-12 grid grid-cols-2 sm:grid-cols-3 gap-4 lg:grid-cols-4 justify-items-stretch">
      {data.map(({ show }) => {
        const isStarred = starredShow.includes(show.id);
        const onStarClick = () => {
          if (isStarred) {
            dispatchStarred({ type: 'REMOVE', showId: show.id });
          } else {
            dispatchStarred({ type: 'ADD', showId: show.id });
          }
        };
        return (
          <ShowCard
            key={show.id}
            id={show.id}
            name={show.name}
            img={show.image}
            summary={show.summary}
            isStarred={isStarred}
            onStarClick={onStarClick}
          />
        );
      })}
    </FadeIn>
  );
};

export default ShowGrid;
