import React, { useState, useEffect } from 'react';
import ActorGrid from '../components/actor/ActorGrid';
import MainPageLayout from '../components/MainPageLayout';
import ShowGrid from '../components/show/ShowGrid';
import { useLastQuery } from '../misc/custom-hooks';
import { fetchApi } from './../misc/config';

const Home = () => {
  // State for aearch input
  const [input, setInput] = useLastQuery('');
  // State for fetched data
  const [result, setResult] = useState(null);
  // State for searchType
  const [searchOption, setSearchOption] = useState('shows');

  useEffect(() => {
    fetchApi(`/search/${searchOption}?q=${input}`).then(result => {
      setResult(result);
    });
  }, [input, searchOption]);

  const onInputChange = event => {
    setInput(event.target.value);
  };
  const onRadioChange = ev => {
    setSearchOption(ev.target.value);
    // onSearch();
  };
  // const onSearch = () => {
  //   // Grab data from api
  //   fetchApi(`/search/${searchOption}?q=${input}`).then(result => {
  //     setResult(result);
  //   });
  // };
  // const onEnterKeyPressed = ev => {
  //   // 13 Is code for Enter key,
  //   if (ev.keyCode === 13) {
  //     onSearch();
  //   }
  // };
  const renderResult = () => {
    if (result && result.length === 0 && input !== '') {
      return <div>No result</div>;
    } else if (result && result.length > 0) {
      return result[0].show ? (
        <ShowGrid data={result} />
      ) : (
        <ActorGrid data={result} />
      );
    }
    return null;
  };
  return (
    <MainPageLayout>
      <div className="mt-8">
        <input
          className="outline-none py-1 px-2 text-center  w-64 rounded-md ring-2 focus:shadow-md ring-gray-300 focus:ring-blue-600 transition caret-blue-600 "
          type="text"
          placeholder="Search here..."
          onChange={onInputChange}
          value={input}
          // onKeyDown={onEnterKeyPressed}
        />
        <div className="flex mt-4 flex-row gap-4 justify-center">
          <label
            className="cursor-pointer inline-flex items-center"
            htmlFor="shows"
          >
            <input
              className="form-radio"
              id="shows"
              type="radio"
              value="shows"
              onChange={onRadioChange}
              checked={searchOption === 'shows'}
            />
            <span className="px-2">Shows</span>
          </label>

          <label
            className="cursor-pointer inline-flex items-center"
            htmlFor="actor"
          >
            <input
              className="form-radio"
              id="actor"
              type="radio"
              value="people"
              checked={searchOption === 'people'}
              onChange={onRadioChange}
            />
            <span className="px-2">Actor</span>
          </label>
        </div>
        {/* <button type="button" onClick={onSearch}>
        Search
      </button> */}
      </div>
      {renderResult()}
    </MainPageLayout>
  );
};

export default Home;
